import { defineComponent, reactive, ref } from "vue";
import { ElMessage } from "element-plus";
import LocalCache from "@/utils/cache";
import store from "@/store";
export default defineComponent({
  setup() {
    let accountInfo = reactive({
      acid: "coderwhy",
      password: "123456"
    }); // 

    const rules = {
      acid: [{
        required: true,
        message: "",
        trigger: "blur"
      }, {
        pattern: /^[a-z0-9]{5,10}$/,
        message: "5-10",
        trigger: "blur"
      }],
      password: [{
        required: true,
        message: "",
        trigger: "blur"
      }, {
        pattern: /^[a-z0-9]{5,10}$/,
        message: "5-10",
        trigger: "blur"
      }]
    }; // 

    const formRef = ref();

    const submitForm = (formRef, pswStatus) => {
      formRef?.validate(valid => {
        if (valid) {
          const userInfo = {
            name: accountInfo.acid,
            password: accountInfo.password
          };

          if (pswStatus) {
            LocalCache.setCache("userInfo", userInfo);
          } else {
            LocalCache.delCache("userInfo");
          }

          store.dispatch("login/accountLoginAction", userInfo);
          ElMessage({
            message: "",
            type: "success"
          });
          console.log("ac submit");
        } else {
          ElMessage({
            message: "",
            type: "error"
          });
          console.log("ac error submit!");
          return false;
        }
      });
    };

    return {
      accountInfo,
      formRef,
      submitForm,
      rules
    };
  }

});